@use "../../base/base" as b;
@use "../../base/mixin" as m;

@keyframes scroll-bar {
  0% {
    opacity: 0;
    transform: scaleY(0);
    transform-origin: top;
  }
  5% {
    opacity: 1;
    transform: scaleY(0);
  }
  50% {
    opacity: 1;
    transform: scaleY(100%);
  }
  95% {
    opacity: 1;
    transform: scaleY(0);
    transform-origin: bottom;
  }
  100% {
    opacity: 0;
    transform: scaleY(0);
    transform-origin: bottom;
  }
}

.mv {
  height: 100vh;
  position: relative;
  z-index: -2;
  background: url(../img/layout/top/flower-blue2.webp) no-repeat center
    center/cover;
  &::after {
    background-color: rgba(0, 0, 0, 0.26);
    content: "";
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
  }
  &__inner {
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: 108rem;
    position: relative;
  }
  &__content {
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  &__title {
    color: #ffffff;
    font-family: "Playfair Display", serif;
    font-size: clamp(4.5rem, 4.0625vw, 8.5rem);
    font-weight: bold;
    letter-spacing: 0.2em;
  }
  &__catch {
    color: #ffffff;
    font-size: clamp(1.6rem, 1.25vw, 4rem);
    margin-top: 1.5rem;
    text-align: center;
    @media screen and (min-width: 600px) {
      margin-top: 2rem;
    }
  }
  &__scroll {
    display: block;
    -ms-writing-mode: tb-lr;
    bottom: 0;
    color: #ffffff;
    font-size: 1.4rem;
    height: 11rem;
    letter-spacing: 0.3em;
    line-height: 2.5;
    padding: 1.5em 0;
    position: absolute;
    right: 3vw;
    width: 3rem;
    writing-mode: vertical-lr;
    z-index: 20;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 1px;
      background: #ffffff;
      @include m.animation(
        $name: scroll-bar,
        $duration: 2.5s,
        $timing-function: cubic-bezier(1, 0, 0, 1),
        $iteration-count: infinite,
        $fill-mode: none
      );
    }
  }
  @media screen and (min-width: 600px) {
    &__scroll {
      height: 14rem;
    }
  }
}
